import { Link } from "react-router-dom";
import React from "react";

import { MDBBreadcrumb, MDBBreadcrumbItem } from "mdbreact";

export default function Trilha(props) {
  return (
    <MDBBreadcrumb>
      <MDBBreadcrumbItem>
        <Link to="/" title="Home">
          Home
        </Link>
      </MDBBreadcrumbItem>
      <MDBBreadcrumbItem active>{props.pagename}</MDBBreadcrumbItem>
    </MDBBreadcrumb>
  );
}
