import React, { useState } from "react";
import CancelIcon from "@material-ui/icons/Cancel";
import AddCircleOutlineIcon from "@material-ui/icons/AddCircleOutline";
import RemoveCircleOutlineIcon from "@material-ui/icons/RemoveCircleOutline";
import { green } from "@material-ui/core/colors";
import { red } from "@material-ui/core/colors";
import { Grid } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";

export default function CartItem(props) {
  const item = props.item;
  const [qtd, setQtd] = useState(1);

  const useStyles = makeStyles(theme => ({
    hideMe: { display: "none" }
  }));
  const classes = useStyles();

  function excludeItem() {
    setQtd(0);
  }

  function remItem() {
    setQtd(qtd - 1);
  }

  function addItem() {
    setQtd(qtd + 1);
  }
  return (
    <Grid container className={qtd === 0 ? classes.hideMe : ""}>
      <Grid item xs={8} className="text-left">
        <CancelIcon style={{ color: red[500] }} onClick={excludeItem} />
        {item.name}
      </Grid>
      <Grid item xs={2} className="text-center">
        <RemoveCircleOutlineIcon
          style={{ color: red[500] }}
          onClick={remItem}
        />
        {qtd}
        <AddCircleOutlineIcon style={{ color: green[500] }} onClick={addItem} />
      </Grid>
      <Grid item xs={2} className="text-right">
        {item.price}
      </Grid>
    </Grid>
  );
}
