import React from "react";
import Box from "@material-ui/core/Box";
import ProductGrouped from "./ProductGrouped";

export default function ProductGroups(props) {
  const produtos = props.produtos;
  let grupos = produtos.filter(produto => {
    return produto.type === "grouped";
  });
  grupos = grupos.filter(produto => {
    return produto.categories[0].id === props.categoria;
  });

  return (
    <Box className="my-5">
      <h5 className="h5-responsive font-weight-bold text-left my-2">
        {props.titulo}
      </h5>
      {grupos.map(grupo => (
        <ProductGrouped key={grupo.id} item={grupo} produtos={produtos} />
      ))}
    </Box>
  );
}
