import React from "react";
import { useSelector, useDispatch } from "react-redux";
import ThumbUpIcon from "@material-ui/icons/ThumbUp";
import { Grid } from "@material-ui/core";

import { addItemAction } from "../../actions";
import Chip from "@material-ui/core/Chip";

export default function ProductSimple(props) {
  const item = props.item;
  let dia = item.attributes.find(sel => sel.name === "Dias");
  const dispatch = useDispatch();
  const cart = useSelector(state => state.cart);

  if (dia) {
    dia = dia.options[0];
  } else {
    dia = "";
  }
  let genero = item.attributes.find(sel => sel.name === "Gênero");
  if (genero) {
    genero = genero.options[0];
  }

  function comprar() {
    return dispatch(addItemAction(item));
  }

  function btComprar() {
    let clicked = cart.items.find(sel => sel.id === item.id);
    if (clicked) {
      return (
        <Chip
          icon={<ThumbUpIcon />}
          color="primary"
          disabled
          className="w-100 success-color"
        />
      );
    } else {
      return (
        <Chip
          color="primary"
          onClick={comprar}
          label="Comprar"
          className="w-100 success-color"
        />
      );
    }
  }
  function btCamarote() {
    return (
      <Grid container className="grey lighten-4">
        <Grid item xs={6} className="text-left p-2">
          {dia} {genero}
        </Grid>
        <Grid item xs={3} className="text-right p-2">
          {item.price}
        </Grid>
        <Grid item xs={3} className="text-right p-2">
          {btComprar()}
        </Grid>
      </Grid>
    );
  }
  function btBloco() {
    return (
      <Grid container className="grey lighten-4">
        <Grid item xs={6} className="text-left p-2">
          {dia}
        </Grid>
        <Grid item xs={3} className="text-right p-2">
          {item.price}
        </Grid>
        <Grid item xs={3} className="text-right p-2">
          {btComprar()}
        </Grid>
      </Grid>
    );
  }
  function btPassaporte() {
    return (
      <Grid container className="grey lighten-4">
        <Grid item xs={6} className="text-left p-2">
          {item.name}
        </Grid>
        <Grid item xs={3} className="text-right p-2">
          {item.price}
        </Grid>
        <Grid item xs={3} className="text-right p-2">
          {btComprar()}
        </Grid>
      </Grid>
    );
  }
  switch (item.categories[0].id) {
    case 16: //camarote
      return btCamarote();
    case 17: //bloco
      return btBloco();
    case 19: //passaporte
      return btPassaporte();

    default:
      return <p>{item.name}</p>;
  }
}
