import React from "react";
import { Paper, Box } from "@material-ui/core";
import ProductSimple from "./ProductSimple";

export default function ProductGroups(props) {
  const produtos = props.produtos;
  const itemPai = props.item;
  const imgurl = itemPai.images[0] ? itemPai.images[0].src : "";
  const imgalt = itemPai.images[0] ? itemPai.images[0].alt : "";
  const grouped_products = itemPai.grouped_products;
  let ItensDoGrupo = getGrupoDeProdutos();

  function getGrupoDeProdutos() {
    let res = [];
    if (grouped_products) {
      Array.prototype.forEach.call(grouped_products, id => {
        let produto = produtos.find(produto => produto.id === id);
        if (produto) res.push(produto);
      });
    }
    return res;
  }

  return (
    <Paper className="my-3">
      <Box display="flex" flexDirection="row" className="amber lighten-2">
        <Box className="p-2">
          <img
            src={imgurl}
            alt={imgalt}
            className="img-fluid"
            style={{ maxHeight: "7vh" }}
          />
        </Box>
        <Box className="p-2 h5-responsive text-center">{itemPai.name}</Box>
      </Box>

      {ItensDoGrupo.map(ItemDoGrupo => (
        <Box key={ItemDoGrupo.id} display="flex" flexDirection="row">
          <ProductSimple item={ItemDoGrupo} />
        </Box>
      ))}
    </Paper>
  );
}
