import React from "react";
import { useSelector } from "react-redux";
import { Container, Slide } from "@material-ui/core";
import ProductGroups from "./ProductGroups";
import Trilha from "../Trilha";
import Carrinho from "../carrinho";

function CompreAqui() {
  let produtos = useSelector(state => state.products.produtos);

  return (
    <Slide direction="up" in={true} mountOnEnter unmountOnExit>
      <div>
        <Trilha pagename="Compre aqui" />
        <h5>Monte o carnaval do seu jeito</h5>
        <ProductGroups
          categoria={16}
          titulo={"CAMAROTES"}
          produtos={produtos}
        />
        <ProductGroups categoria={17} titulo={"BLOCOS"} produtos={produtos} />
        <Container className="fixed-bottom">
          <Carrinho />
        </Container>
      </div>
    </Slide>
  );
}

export default CompreAqui;
